.modal-terminal {
  z-index: 1000;
  background-color: #fff;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
  border: 17vh solid #000c;
  border-width: 17vh 17vw;
  width: 66vw;
  height: 66vh;
  margin: -17vh -17vw;
  position: fixed;
  top: 17vh;
  left: 17vw;
  overflow: auto;
}

.modal-terminal button {
  z-index: 1001;
  position: fixed;
  top: 17vh;
  right: 17vw;
}

.modal-terminal pre, .modal-terminal button {
  margin: 5px;
}

